


































import { fetchWeatherByCoordinates } from '@/api/openweather';
import { Component, Vue } from 'vue-property-decorator';

@Component({
  props: {
    city: {
      type: Object,
      required: true,
    },
  },
  components: {},
})
export default class CityWidget extends Vue {
  city!: City;
  weather!: Nullable<CityWeather>;

  data(): Data {
    return {
      weather: null,
    };
  }

  created() {
    this.getWeather();
  }

  get weatherWindDir() {
    if (this.weather) {
      const compassSector = [
        'N',
        'NNE',
        'NE',
        'ENE',
        'E',
        'ESE',
        'SE',
        'SSE',
        'S',
        'SSW',
        'SW',
        'WSW',
        'W',
        'WNW',
        'NW',
        'NNW',
        'N',
      ];
      const index = parseInt((this.weather.wind.deg / 22.5).toFixed(0));
      return compassSector[index];
    }
    return '';
  }

  get iconSrc() {
    if (this.weather) {
      return `https://openweathermap.org/img/wn/${this.weather.icon}@2x.png`;
    }
    return '';
  }

  get dewPoint() {
    if (this.weather) {
      const tem2 = this.weather.temp;
      const r = this.weather.humidity;
      const tem = -1.0 * tem2;
      const es = 6.112 * Math.exp((-1.0 * 17.67 * tem) / (243.5 - tem));
      const ed = (r / 100.0) * es;
      const eln = Math.log(ed / 6.112);
      const dt = (-243.5 * eln) / (eln - 17.67);
      return dt.toFixed(1);
    }
    return 0;
  }

  async getWeather() {
    const { lat, lon } = this.city.coords;
    const data = await fetchWeatherByCoordinates(lat, lon);
    const { temp, feels_like, temp_min, temp_max, pressure, humidity } = data.main;

    this.weather = {
      temp,
      feels_like,
      temp_min,
      temp_max,
      pressure,
      humidity,
      text: data.weather[0].main,
      icon: data.weather[0].icon,
      wind: data.wind,
      visibility: data.visibility,
    };
  }
}

type Data = {
  weather: Nullable<CityWeather>;
};
