











































import { Component, Vue } from 'vue-property-decorator';
import draggable from 'vuedraggable';

import { fetchWeatherByCityName } from '@/api/openweather';
import store from '@/store';

type VForm = Vue & {
  validate(): boolean;
};

@Component({
  components: { draggable },
  props: {
    cities: {
      type: Array,
      required: true,
    },
  },
})
export default class Settings extends Vue {
  cities!: City[];
  newCityName!: string;
  $refs!: {
    form: VForm;
  };
  data() {
    return {
      newCityName: '',
      inputRules: [(v: string) => (v != null && v != '') || 'Please enter city name'],
    };
  }

  public get citiesList() {
    return this.cities;
  }

  public set citiesList(cities: City[]) {
    store.commit('setCities', cities);
  }

  async handleEnteringNewCity() {
    if (!this.$refs.form.validate()) {
      return false;
    }
    try {
      const data = await fetchWeatherByCityName(this.newCityName);

      const city: City = {
        name: data.name,
        country: {
          code: data.sys.country,
        },
        coords: data.coord,
      };

      this.$emit('add', city);
    } catch (error) {
      alert('No city found!');
    } finally {
      this.newCityName = '';
    }
  }

}
